.soc-big {
	position: relative;
	display: flex;
	justify-content: flex-start;
	width: calc(100% + 30px);
	margin-left: -30px;
	flex-flow: row nowrap;
	margin-bottom: 98px;
	@media (max-width: 1150px) {
		width: 100%;
		max-width: 512px;
		margin-left: 0;
		flex-flow: column nowrap;
		margin-left: auto;
		margin-right: auto;
	}
	@include max(md) {
		margin-bottom: 47px;
	}
	&__item {
		position: relative;
		display: flex;
		flex: 1 0 33.333%;
		max-width: calc(33.333% - 30px);
		margin-left: 30px;
		justify-content: center;
		align-items: flex-start;
		padding-left: 32px;
		min-height: 120px;
		transition: all .87s;
		text-decoration: none;
		@include max(sm) {
			min-height: 100px;
			margin-bottom: 16px;
		}
		@media (max-width: 1150px) {
			flex: 1 0 100%;
			max-width: calc(100% - 0px);
			width: 100%;
			margin-left: 0px;
		}
		&:last-child {
			@media (max-width: 1150px) {
				margin-bottom: 0;
			}

		}
		&:hover {
			transition: all .87s;
			.soc-big__item__title {
				color: $white;
			}
			.soc-big__item__picture {
				background: $white;
			}
		}
		&--tg {
			.soc-big__item__picture {
				background: #259CD8;
			}
			&:hover {
				background: #259CD8;
				.soc-big__item__picture {
					svg {
						path {
							fill: #259CD8;
						}
					}
				}

			}
			
		}
		&--mail {
			.soc-big__item__picture {
				background: $red;
			}
			&:hover {
				background: $red;
				.soc-big__item__picture {
					svg {
						path {
							fill: $red;
						}
					}
				}

			}
		}
		&--wt {
			.soc-big__item__picture {
				background: #25CB63;
			}
			&:hover {
				background: #25CB63;
				.soc-big__item__picture {
					svg {
						path {
							fill: #25CB63;
						}
					}
				}

			}
		}
		&__title {
			color: #7F8589;
			font-size: 14px;
			font-style: normal;
			font-weight: 500;
			line-height: 150%;	
		}
		&__subtitle {
			color: $white;
			font-size: 22px;
			font-style: normal;
			font-weight: 500;
			line-height: 150%;
			@include max(sm) {
				font-size: 18px;
				font-style: normal;
				font-weight: 500;
				line-height: 150%;				
			}		
		}
		&__picture {
			position: absolute;
			right: 22px;
			width: 48px;
			height: 48px;	
			top: auto;
			display: flex;
			justify-content: center;
			align-items: center;
			background: $red;
			border-radius: 50%;
		}
	}
}
